<template>
  <div id="dashboard">
      <h3>Dashboard</h3>

      <!-- Display data from firebase -->
      <ul class="collection with-header">
          <li class="collection-header"><h4>Employees</h4></li>
          <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
              <div class="chip">{{ employee.dept }}</div>
              {{ employee.employee_id }} - {{ employee.name }}

            <!-- icons link to view the employees -->
            <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: { employee_id: employee.employee_id }}">
                <i class="material-icons">remove_red_eye</i>
            </router-link>

          </li>
      </ul>
      <!-- floating button -->
    <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red pulse">
        <i class="large material-icons">person_add</i>
        </router-link>
    </div>

  </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'dashboard',
    data() {
        return {
            employees: []
        }
    },
    created () {
        db.collection('employees').orderBy('dept').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position

                }
                this.employees.push(data)
            })
        })
    }
}
</script>



